import { injectable } from 'inversify';
import ApiService from '@jwp/ott-common/src/services/ApiService';

import { getDataOrThrow } from '../../../utils/api';
import type { GetPlaylistParams, Playlist, PlaylistItem } from '../../../../types/playlist';
import type { GetContentSearchParams } from '../../../../types/content-list';
import type { AdSchedule } from '../../../../types/ad-schedule';
import type { EpisodeInSeries, EpisodesWithPagination, GetSeriesParams, Series } from '../../../../types/series';
import env from '../../../env';

import { getApolloClient } from './apollo-client';
import { GetCollectionDocument, GetItemDocument, GetItemsDocument, KeywordSearchDocument } from './types/catalog-service-auto-generated';
import type { GetItemQuery, GetItemQueryVariables, GetItemsQuery, GetItemsQueryVariables, GetCollectionQuery, GetCollectionQueryVariables, KeywordSearchQuery, KeywordSearchQueryVariables, Movie, Episode, Tvshow, Season as AxSeason } from './types/catalog-service-auto-generated';

const PAGE_LIMIT = 20;
const CATALOG_API_BASE_URL = "https://mosaic-ott-catalog.axprod.net/graphql"
const IMAGE_API_BASE_URL = "https://image.service.eu.axinom.net"
const apolloClient = getApolloClient(CATALOG_API_BASE_URL);
const notImplemented = () => {
  throw new Error("Not Implemented");
};

@injectable()
export default class AxinomApiService extends ApiService {
  
  private transformToPlaylistItem = (
    item: Episode | Movie | Tvshow | AxSeason,
    contentType: 'movie' | 'episode' | 'series' | 'season',
    isTrailer?: boolean
  ): PlaylistItem => {

    const images = (item?.images?.nodes as Array<{ path: string; type: string; width: number }> | undefined)
    ?.filter(node => node.path)// && node.width)
    .map(node => ({
      src: IMAGE_API_BASE_URL + node.path,
      type: node.type,
      width: node.width
    })) ?? [];

    type VideoNode = { 
      hlsManifest: string; 
      dashManifest: string;
      type: string; 
      lengthInSeconds: number;
    };
      
    const sources = (
      'videos' in item && Array.isArray(item?.videos?.nodes)
        ? (item.videos.nodes as VideoNode[]) 
            .filter(node => (node.hlsManifest || node.dashManifest) && (isTrailer ? node.type === "TRAILER" : node.type === "MAIN"))
            .map(node => ({
              file: node.hlsManifest ?? node.dashManifest,
              type: node.hlsManifest  ? 'application/vnd.apple.mpegurl' : 'application/dash+xml',
              lengthInSeconds: node.lengthInSeconds
            }))
        : []
    );

    const has_any_trailer = 'videos' in item && Array.isArray(item?.videos?.nodes) ?
     ((item.videos.nodes as VideoNode[]).find(node => (node.hlsManifest || node.dashManifest) && (node.type === "TRAILER")) ?? false) : false;
 
   const genres = [''];
   genres.pop();
   if (contentType == "movie") {
      (item as Movie).genres.nodes.forEach(node => {
        genres.push(node.movieGenre?.title ?? '?');
    })    
  }
  else if (contentType == "series") {
    (item as Tvshow).genres.nodes.forEach(node => {
      genres.push(node.tvshowGenre?.title ?? '?');
  })    
}
   


    return {
      description: item?.description ?? '',
      duration: sources.length > 0 ? sources[0]?.lengthInSeconds ?? 0 : 0,
      feedid: 'na',     
      genre: genres.join(',  '),
      image: '',
      link: '',
      mediaid: item?.id,
      pubdate: 0,
      tags:  genres.join(','), 
      title: 'index' in item && contentType === 'season' ? item.index?.toString() ?? '' : ('title' in item ? (item.title ?? '') : ''),
      contentType,
      cardImage: images.find(p => p.type === "COVER")?.src || '/404.png',
      backgroundImage: images.find(p => p.type === "TEASER")?.src || '/404.png',
      images,
      sources,      
      ...(contentType === 'movie' && has_any_trailer && { trailerId: "TRAILER_" + item?.id }), 
      cast: item?.cast,
      genres: genres.filter(genre => genre != 'Action') // a bit misued genre in axinom samples
    };
  };

  tranformEpisodeToPlayListItem = (episode: Episode) => {
    return this.transformToPlaylistItem(episode, 'episode');
  };

  tranformMovieToPlayListItem = (movie: Movie, isTrailer?: boolean) => {
    return this.transformToPlaylistItem(movie, 'movie', isTrailer);
  };

  tranformSeasonToPlayListItem = (season: AxSeason) => {
    return this.transformToPlaylistItem(season, 'season');
  };

  tranformTvShowToPlayListItem = (tvshow: Tvshow) => {
    return this.transformToPlaylistItem(tvshow, 'series');
  };

  transformAxinomMediaItem = (query_result: GetItemQuery, isTrailer?: boolean) => {
    if (query_result.movie) {
      return this.tranformMovieToPlayListItem(query_result.movie as Movie, isTrailer);
    } else if (query_result.episode) {
      return this.tranformEpisodeToPlayListItem(query_result.episode as Episode);
    } else if (query_result.tvshow) {
      return this.tranformTvShowToPlayListItem(query_result.tvshow as Tvshow);
    } else if (query_result.season) {
      return this.tranformSeasonToPlayListItem(query_result.season as AxSeason);
    }
    return {
      title: "not found"
    };
  };

  /**
   * Get watchlist by playlistId
   */
  getMediaByWatchlist = async ({
    playlistId,
    mediaIds,
    token,
    language,
  }: {
    playlistId: string;
    mediaIds: string[];
    token?: string;
    language?: string;
  }): Promise<PlaylistItem[] | undefined> => {
    if (!mediaIds?.length) {
      return [];
    }
    return notImplemented();    
  };

  /**
   * Get media by id
   */
  getMediaById = async ({
    id,
    token,
    drmPolicyId,
    language,
  }: {
    id: string;
    token?: string;
    drmPolicyId?: string;
    language?: string;
  }): Promise<PlaylistItem | undefined> => {
        
    const result = await apolloClient.query<GetItemQuery, GetItemQueryVariables>({
      context: { headers: language ? { 'mosaic-locale': language, } : undefined, },
      query: GetItemDocument,
      variables: { id: id.replace("TRAILER_",'') },
      fetchPolicy: 'no-cache',
    });

    if (result.errors) {      
      throw new Error('MediaItem not found');
    }        
    const transformedMediaItemAxinom = this.transformAxinomMediaItem(result.data, id.startsWith("TRAILER_"));
    return transformedMediaItemAxinom as PlaylistItem;
  };

  /**
   * Get media by id with passport
   * Axinom: Not supported (yet), the function passed the id to getMediaById
   */
  getMediaByIdWithPassport = async ({
    id,
    siteId,
    planId,
    passport,
    language,
  }: {
    id: string;
    siteId: string;
    planId: string;
    passport: string;
    language?: string;
  }): Promise<PlaylistItem | undefined> => {            
    return await this.getMediaById({id: id, token: undefined, drmPolicyId: undefined, language: language ?? undefined});
  };

  /**
   * Get series by id
   * @param {string} id
   * @param params
   */
  getSeries = async (id: string, params: GetSeriesParams = {}): Promise<Series | undefined> => {
    if (!id) {
      throw new Error('Series ID is required');
    }

    const result = await apolloClient.query<GetItemQuery, GetItemQueryVariables>({
      query: GetItemDocument,
      variables: { id },
      fetchPolicy: 'no-cache',
    });

    const tvshow = result?.data?.tvshow;
    if (!tvshow) return undefined;

    const seasons = tvshow.seasons?.nodes
      ?.filter(season => !params.season || season.index === params.season)
      .map(season => {
        const seasonDuration = season.episodes?.nodes?.reduce((duration, episode) => {
          const video = episode.videos?.nodes?.find(v => v.type === 'MAIN');
          return duration + (video?.lengthInSeconds ?? 0);
        }, 0) ?? 0;

        return {
          season_id: season.id,
          season_number: season.index ?? 1,
          season_title: `Season ${season.index ?? 1}`,
          season_description: season.synopsis ?? '',
          episode_count: season.episodes?.nodes?.length ?? 0,
          total_duration: seasonDuration,
        };
      }) ?? [];

    const episodeCount = seasons.reduce((count, season) => count + (season.episode_count ?? 0), 0);
    const totalDuration = seasons.reduce((total, season) => total + (season.total_duration ?? 0), 0);

    return {
      series_id: tvshow.id,
      episode_count: episodeCount,
      season_count: seasons.length,
      total_duration: totalDuration,
      seasons,
    };
  };

  /**
   * Get all series for the given media_ids
   */
  getSeriesByMediaIds = async (mediaIds: string[]): Promise<Record<string, EpisodeInSeries[]> | undefined> => {    
    const result = await apolloClient.query<GetItemsQuery, GetItemsQueryVariables>({
      query: GetItemsDocument,
      variables: { ids: mediaIds },
      fetchPolicy: 'no-cache',
    });
    
    const createMediaObject = (entity : Record<string, EpisodeInSeries[]>,  id: string, series_id: string, season_number: number, episode_number: number) => {      
        entity[id] = [
          {
            series_id: series_id,
            episode_number: episode_number,
            season_number: season_number,
          } as EpisodeInSeries,
        ]    
    };

    const media_object: Record<string, EpisodeInSeries[]> | undefined = {};
    result.data.episodes?.nodes.forEach(episode=> {
      createMediaObject(media_object,
        episode.id,
        episode.season?.tvshowId ?? '', 
        episode?.season?.index ?? 0, 
        episode?.index ?? 0);        
      }
    );
    return media_object;
  }

  /**
   * Get all episodes of the selected series (when no particular season is selected or when episodes are attached to series)
   * Problem: Axinom Mosaic does not have paged episodes per serie .. we have to download entire serie and then kind of mock the paging :/
   */
  getEpisodes = async ({
    seriesId,
    pageOffset,
    pageLimit = PAGE_LIMIT,
    afterId,
    language,
  }: {
    seriesId: string | undefined;
    pageOffset?: number;
    pageLimit?: number;
    afterId?: string;
    language?: string;
  }): Promise<EpisodesWithPagination> => {
    if (!seriesId) {
      throw new Error('Series ID is required');
    }

    const result = await apolloClient.query<GetItemQuery, GetItemQueryVariables>({
      context: { headers: language ? { 'mosaic-locale': language, } : undefined, },
      query: GetItemDocument,      
      variables: { id: seriesId },
      fetchPolicy: 'no-cache',
    });

    if (result.errors) {      
      throw new Error('MediaItem not found');
    }    
    const serie = result.data?.tvshow;
    
    const episodes: PlaylistItem[] = [];
    serie?.seasons.nodes.forEach(season => {
      season.episodes.nodes.forEach(season_episode => { 
        if (season_episode!= null) {
            const item = {
            ...this.tranformEpisodeToPlayListItem(season_episode as Episode),
            seasonNumber: season?.index?.toString() ?? '',
            episodeNumber: season_episode?.index?.toString() ?? ''
            } as PlaylistItem;
            episodes.push(item);
        }
      });
    });
    
 const totalEpisodes = episodes.length ?? 1;
 const totalPages = Math.max(1, Math.ceil(totalEpisodes / pageLimit));
 const currentOffset = Math.min((pageOffset ?? 0 + 1), totalPages); 
 const startIndex = (currentOffset - 1) * pageLimit;
 const endIndex = Math.min(startIndex + pageLimit, totalEpisodes);
 const filtered_episodes = episodes.slice(startIndex, endIndex);

  const pagination = { 
    page: (pageOffset ?? 0) + 1,
    page_limit: pageLimit,
    total: totalEpisodes
   };

    return {
      episodes: filtered_episodes,
      pagination: pagination,
    };
  };

  /**
   * Get season of the selected series
   */
  getSeasonWithEpisodes = async ({
    seriesId,
    seasonNumber,
    pageOffset,
    pageLimit = PAGE_LIMIT,
    language,
  }: {
    seriesId: string | undefined;
    language: string;
    seasonNumber: number;
    pageOffset?: number;
    pageLimit?: number;
  }): Promise<EpisodesWithPagination> => {    
    if (!seriesId) {
      throw new Error('Series ID is required');
    }

    //Load serie with id - process it, but just the season number
    const result = await apolloClient.query<GetItemQuery, GetItemQueryVariables>({
      context: { headers: language ? { 'mosaic-locale': language, } : undefined, },
      query: GetItemDocument,      
      variables: { id: seriesId },
      fetchPolicy: 'no-cache',
    });

    if (result.errors) {      
      throw new Error('MediaItem not found');
    }

    const episodes: PlaylistItem[] = [];
    result.data.tvshow?.seasons.nodes.forEach(season => {
      if (season.index == seasonNumber) {
      season.episodes.nodes.forEach(season_episode => { 
        if (episodes.length < pageLimit && season_episode!= null) {
            const item = {
            ...this.tranformEpisodeToPlayListItem(season_episode as Episode),
            seasonNumber: season?.index?.toString() ?? '',
            episodeNumber: season_episode?.index?.toString() ?? ''
            } as PlaylistItem;
            episodes.push(item);
        }
      });
    }
    });
   
  const pagination = { 
    page: (pageOffset ?? 0) + 1,
    page_limit: pageLimit,
    total: episodes.length ?? 1
   };

    return {
      episodes: episodes,
      pagination: pagination,
    };
  };

  getAdSchedule = async (id: string | undefined | null): Promise<AdSchedule | undefined> => {
    if (!id) {
      throw new Error('Ad Schedule ID is required');
    }

    const url = env.APP_API_BASE_URL + `/v2/advertising/schedules/${id}.json`;
    const response = await fetch(url, { credentials: 'omit' });

    return (await getDataOrThrow(response)) as AdSchedule;
  };



  doKeyWordSearch = async (params: GetPlaylistParams = {}, language: string = env.APP_DEFAULT_LANGUAGE): Promise<Playlist | undefined> => {

    //First we query get a playlist    
    const result = await apolloClient.query<KeywordSearchQuery, KeywordSearchQueryVariables>({
      context: { headers: language ? { 'mosaic-locale': language, } : undefined, },
      query: KeywordSearchDocument,
      variables: { keyword:  params.search ?? '' },
      fetchPolicy: 'no-cache',
    });

    const transformedCollection: Playlist = {
      playlist: [],
      title: 'Search result'     
    }
    
    transformedCollection.playlist = [];

    result.data.movies?.nodes.forEach((item) => {      
      transformedCollection.playlist.push(this.tranformMovieToPlayListItem(item as Movie));            
    });
    result.data.episodes?.nodes.forEach((item) => {      
      transformedCollection.playlist.push(this.tranformEpisodeToPlayListItem(item as Episode));            
    });
    result.data.tvshows?.nodes.forEach((item) => {      
      transformedCollection.playlist.push(this.tranformTvShowToPlayListItem(item as Tvshow));
    });

    return transformedCollection;
  }


  /**
   * Get playlist by id
   */
  getPlaylistById = async (id?: string, params: GetPlaylistParams = {}, language: string = env.APP_DEFAULT_LANGUAGE): Promise<Playlist | undefined> => {
    if (!id) {
      return undefined;
    }

  if (id == "KEYWORD-SEARCH") {
    return await this.doKeyWordSearch(params, language);
  }

  let related_media_item: PlaylistItem | undefined = undefined;
  if (params.related_media_id) {    
    related_media_item = await this.getMediaById({id: params?.related_media_id, token: undefined, drmPolicyId:undefined, language: language});    
  }

  //First we query get a playlist  
  const result = await apolloClient.query<GetCollectionQuery, GetCollectionQueryVariables>({
    context: { headers: language ? { 'mosaic-locale': language, } : undefined, },
    query: GetCollectionDocument,
    variables: { id:  id },
    fetchPolicy: 'no-cache',
  });

  if (result.errors) {      
    throw new Error('MediaItem not found');
  }
  const collection = result.data?.collection;

    const transformedCollection: Playlist = {
      description: collection?.description ?? '',      
      feedid: collection?.id,      
      title: params.related_media_id || !collection?.title ? "More like this" : collection?.title,
      playlist: [],      
      cardImageAspectRatio: "2:3",            
    }        
    
    transformedCollection.playlist = [];
    collection?.items?.nodes.forEach((item) => {      
      switch (item.relationType) {
        case "MOVIE":
          if (item.movie!=null) {            
            const movie_playlist_item = this.tranformMovieToPlayListItem(item.movie as Movie);
            if (!related_media_item || this.arePlayListItemsRelated(related_media_item, movie_playlist_item)) {
              transformedCollection.playlist.push(movie_playlist_item);
            }
          }
          break;
        case "EPISODE":
            if (item.episode!=null) {
              transformedCollection.playlist.push(this.tranformEpisodeToPlayListItem(item.episode as Episode));
            }
            break;
        case "TVSHOW":
            if (item.tvshow!=null) {
              transformedCollection.playlist.push(this.tranformTvShowToPlayListItem(item.tvshow as Tvshow));
            }
            break;
      }      
    });  
    return transformedCollection;
  };

  arePlayListItemsRelated = (a: PlaylistItem, b: PlaylistItem): boolean => {    
    // We do the related item comparison client side over whole movie list, based on genre as a temp solution, as the catalog service does not expose an api for this for now
    return Array.isArray(a.genres) && Array.isArray(b.genres) && a.mediaid != b.mediaid && a.genres.some(item => (b.genres as Array<string>).includes(item));    
  }

  getContentList = async ({ id, siteId, language }: { id: string | undefined; siteId: string; language: string }): Promise<Playlist | undefined> => {
    if (!id || !siteId) {
      throw new Error('List ID and Site ID are required');
    }
    return await this.getPlaylistById(id, undefined, language);
  };

  getContentSearch = async ({ siteId, params }: { siteId: string; params: GetContentSearchParams }) => {     
    return await this.doKeyWordSearch({ search: params.searchTerm }) as Playlist;
  };
}
